<template>
    <div class="container-fluid p-4">
        <div class="row">
            <div class="col-md-3">
                <div class="input-group mb-3">
                    <span class="input-group-text">Размер модуля</span>
                    <input type="number" min="10" max="10000" step="1" class="form-control" v-model="sizeX" @change="rebuildScreenImage"/>
                    <span class="input-group-text">Х</span>
                    <input type="number" min="10" max="10000" step="1" class="form-control" v-model="sizeY" @change="rebuildScreenImage"/>
                    <span class="input-group-text">px</span>
                </div>

                <div class="input-group mb-3">
                    <span class="input-group-text">Кол-во в ширину</span>
                    <input type="number" min="1" max="1000" step="1" class="form-control" v-model="countX" @change="rebuildScreenImage"/>
                    <span class="input-group-text">шт</span>
                </div>

                <div class="input-group mb-3">
                    <span class="input-group-text">Кол-во в высоту</span>
                    <input type="number" min="1" max="1000" step="1" class="form-control" v-model="countY" @change="rebuildScreenImage"/>
                    <span class="input-group-text">шт</span>
                </div>

                <div class="input-group mb-3">
                    <span class="input-group-text">Текст:</span>
                    <input type="text" class="form-control" v-model="text" @input="rebuildScreenImage" @change="rebuildScreenImage"/>
                    <span class="input-group-text">Размер:</span>
                    <input type="number" min="5" max="1000" step="1" class="form-control" v-model="textSize" @input="rebuildScreenImage" @change="rebuildScreenImage"/>
                    <span class="input-group-text">px</span>
                </div>

                <div class="input-group mb-3">
                    <span class="input-group-text">Вырезать:</span>
                    <input type="text" placeholder="номера модулей через запятую" class="form-control" v-model="exclude" @input="rebuildScreenImage" @change="rebuildScreenImage"/>
                </div>
            </div>

            <div class="col-md-3">
                <div class="mb-1 form-check">
                    <label><input 
                        type="checkbox" 
                        class="form-check-input" 
                        v-model="isShowCirc" 
                        @change="rebuildScreenImage"> Показать круг</label>
                </div>
                <div class="mb-1 form-check">
                    <label><input 
                        type="checkbox" 
                        class="form-check-input" 
                        v-model="isShowLines" 
                        @change="rebuildScreenImage"> Показать диагонали</label>
                </div>
                <div class="mb-1 form-check">
                    <label><input 
                        type="checkbox" 
                        class="form-check-input" 
                        v-model="isShowNumbers" 
                        @change="rebuildScreenImage"> Показать номера модулей</label>
                </div>
                <div class="mb-1 form-check">
                    <label><input 
                        type="checkbox" 
                        class="form-check-input" 
                        v-model="isShowResolutiuon" 
                        @change="rebuildScreenImage"> Показать разрешение</label>
                </div>
                <div class="mb-3 form-check">
                    <label><input 
                        type="checkbox" 
                        class="form-check-input" 
                        v-model="isShowText" 
                        @change="rebuildScreenImage"> Показать текст</label>
                </div>

                <button class="btn btn-success" @click="download">Скачать png</button>
            </div>
        </div>

        <canvas ref='screen'></canvas>

        <Footer/>
    </div>
</template>

<script>
import Footer from "@/components/Footer.vue"

export default {
  name: 'App',
  components: {
    Footer
  },
  data() {
    return {
        colors: ["#00003f", "#003f00", "#3f3f00", "#3f0000", "#3f003f"],
        screenWidth: 1280,
        screenHeight: 896,
        colorsInModules: [],
        sizeX: 128,
        sizeY: 128,
        countX: 10,
        countY: 7,
        screen: null,
        ctx: null,
        text: "Test Screen",
        isShowCirc: true,
        isShowLines: true,
        isShowResolutiuon: true,
        isShowNumbers: true,
        isShowText: true,
        textSize: 150,
        exclude: "",
        modules: []
    }
  },
  mounted: function(){
    this.rebuildScreenImage()
  },
  methods: {
    rebuildScreenImage() {
        this.screen = this.$refs['screen'];
        this.ctx = this.screen.getContext('2d');
        this.screen.width  = this.sizeX * this.countX;
        this.screen.height = this.sizeY * this.countY;
        this.screenWidth = this.screen.width;
        this.screenHeight = this.screen.height;

        let totalNumber = 1;
        let y = 0;

        for(let row = 1; row <= this.countY; row++) {
            let x = 0;
            for(let col = 1; col <= this.countX; col++) {
                let color = this.getColor(x, y);
                this.colorsInModules[`${x}#${y}`] = color;
                this.showModule(x, y, color, totalNumber.toString());
                this.modules[totalNumber] = {x: x, y: y};
                x = x + this.sizeX;
                totalNumber++;
            }
            y = y + this.sizeY;
        }

        this.showCopy();
        if (this.isShowCirc) this.circle();
        if (this.isShowLines) this.lines();
        if (this.isShowResolutiuon) this.showResolution();
        if (this.isShowText) this.centralText(this.text, this.textSize);

        if (this.exclude !== "") this.removeModules()
    },
    removeModules() {
        let self = this;
        this.exclude.split(",").forEach(function (el){
            let moduleId = Number(el.replace(/\s/g, ""));
            if (moduleId === 0 || moduleId === NaN || moduleId === undefined || moduleId === null) return;
            let module = self.modules[moduleId];
            if (module === NaN || module === undefined || module === null) return;
            self.ctx.clearRect(module.x, module.y, self.sizeX, self.sizeY);
        })
    },
    getColor(x, y){
        let leftX = x - this.sizeX;
        let leftY = y;
        let topX = x;
        let topY = y - this.sizeY;

        let colorLeft = this.colorsInModules[`${leftX}#${leftY}`];
        if (colorLeft === undefined) colorLeft = "";
        let colorTop = this.colorsInModules[`${topX}#${topY}`];
        if (colorTop === undefined) colorTop = "";

        while(true) {
            let newColor = this.colors[Math.floor(Math.random()*this.colors.length)];
            if (newColor !== colorTop && newColor !== colorLeft) return newColor;
        }
    },

    showModule(x, y, color, text) {
        this.ctx.fillStyle = color;
        this.ctx.fillRect(x + 1, y + 1, this.sizeX - 2, this.sizeY - 2);
        this.ctx.strokeStyle = "rgb(255, 255, 255)";
        this.ctx.lineWidth = 1;
        this.ctx.strokeRect(x + 1, y + 1, this.sizeX - 2, this.sizeY - 2);
        this.ctx.fillStyle = "rgb(255, 255, 255)";
        this.ctx.font = "14px Tahoma";
        this.ctx.textBaseline = 'bottom';
        this.ctx.textAlign = "left";
        if (this.isShowNumbers) this.ctx.fillText(text, x + 5, y + this.sizeY - 5);
    },

    circle() {
        let circle = new Path2D();
        if (this.screenWidth >= this.screenHeight) {
            circle.arc(this.screenWidth / 2, this.screenHeight / 2, this.screenHeight / 2 - 2, 0, 2 * Math.PI);
        }
        else {
            circle.arc(this.screenWidth / 2, this.screenHeight / 2, this.screenWidth / 2 - 2, 0, 2 * Math.PI);
        }
        
        this.ctx.stroke(circle);
    },

    centralText(text, size) {
        this.ctx.fillStyle = "#dcb422";
        this.ctx.font = `bold ${size}px Tahoma`;
        this.ctx.textBaseline = 'middle';
        this.ctx.textAlign = "center";
        this.ctx.shadowColor = "#000";
        this.ctx.shadowBlur = 0;
        this.ctx.shadowOffsetX = 3;
        this.ctx.shadowOffsetY = 3;
        this.ctx.fillText(text, this.screenWidth/2, this.screenHeight/2);
    },

    lines() {
        let lines = new Path2D();
        lines.moveTo(0, 0);
        lines.lineTo(this.screenWidth, this.screenHeight);
        lines.lineWidth = 1;
        this.ctx.stroke(lines);
        lines.moveTo(this.screenWidth, 0);
        lines.lineTo(0, this.screenHeight);
        lines.lineWidth = 1;
        this.ctx.stroke(lines);
    },

    showResolution() {
        this.ctx.fillStyle = "#dcb422";
        this.ctx.font = "20px Tahoma";
        this.ctx.textBaseline = 'top';
        this.ctx.textAlign = "left";
        this.ctx.shadowColor = "#000";
        this.ctx.shadowBlur = 0;
        this.ctx.shadowOffsetX = 0;
        this.ctx.shadowOffsetY = 0;
        this.ctx.fillText(`${this.screenWidth}x${this.screenHeight}`, 5, 5);
    },

    showCopy() {
        this.ctx.font = "10px Tahoma";
        this.ctx.fillStyle = "rgba(255, 255, 255, 0.3)";
        this.ctx.textBaseline = 'top';
        this.ctx.textAlign = "right";
        this.ctx.fillText(`sysolyatin.com`, this.screenWidth - 5, 5);
    },

    download() {
        let canvasUrl = this.screen.toDataURL();
        const createEl = document.createElement('a');
        createEl.href = canvasUrl;
        createEl.download = `${this.text.replace(/\s/g, "_")}_${this.screenWidth}x${this.screenHeight}`;
        createEl.click();
        createEl.remove();
    }
}
}
</script>

<style>
@font-face {
    font-family: 'Ggg';
    src: url('./fonts/ggg.ttf') format('truetype'); 
}

body {
    font-family: Ggg, "Times New Roman", Times, serif;
}
</style>
